import React, { useEffect, useState } from "react"
import Box from '@mui/material/Box'

import withStyles from '@mui/styles/withStyles';
import Button from '@mui/material/Button'
import Typography from '@mui/material/Typography'

import useMediaQuery from '@mui/material/useMediaQuery'
import ExpandMoreIcon from '@mui/icons-material/ExpandMore'
import ExpandLessIcon from '@mui/icons-material/ExpandLess'


import { grey, green } from '@mui/material/colors';


const MoreButton = withStyles({
    root: {
        zIndex: 2,
        color: 'white',
        height: '35px',
        fontSize: '0.7rem',

        '&:hover': {
            backgroundColor: grey[300]
        }
    },
    label: {
        textTransform: 'capitalize',
    },
})(Button)

export const ButtonHandler = ({ toggle, setToggle }) => {

    const handleClick = () => {
        setToggle(!toggle)
    }


    return (<MoreButton onClick={handleClick}>
        <Typography style={{ fontWeight: 'bold', fontSize: '0.8rem', color: green[200] }}>
            {!toggle && "Montrer la suite"}
            {toggle && "Réduire le texte"}

        </Typography>
        <Box position="relative" top="2px">
            {!toggle && <ExpandMoreIcon style={{ color: green[200] }} />}
            {toggle && <ExpandLessIcon style={{ color: green[200] }} />}
        </Box>
    </MoreButton>)

}


const ShowLess = ({ text, showMaxSize, handleCutIf }) => {


    const matches = useMediaQuery(theme => theme.breakpoints.up('sm'))
    const [cutIntro, setCutIntro] = useState(false)
    const [showToggle, setshowToggle] = useState(false)

    const handleClick = () => {
        setshowToggle(!showToggle)
    }


    useEffect(() => {
        if (text) {
            const words = text.split(' ')
            if (words.length > showMaxSize)
                setCutIntro(true)
            else
                setCutIntro(false)
        }


    }, [matches])

    if (!handleCutIf)
        return (<Box>
            <Typography>
                {text}
            </Typography>
        </Box>)


    return (<Box>
        <Typography>
            {cutIntro ? showToggle ? text : text.split(' ').slice(0, showMaxSize).join(' ') + ' ...' : text}
        </Typography>
        <Box width="100%" marginTop="20px">

            <Box display="flex" flexDirection="row" justifyContent='center' alignContent='center' >
                {cutIntro && <MoreButton onClick={handleClick}>
                    <Typography style={{ fontWeight: 'bold', fontSize: '0.8rem', color: green[200] }}>
                        {!showToggle && "Montrer la suite"}
                        {showToggle && "Réduire le texte"}

                    </Typography>
                    <Box position="relative" top="2px">
                        {!showToggle && <ExpandMoreIcon style={{ color: green[200] }} />}
                        {showToggle && <ExpandLessIcon style={{ color: green[200] }} />}
                    </Box>
                </MoreButton>}


            </Box>



        </Box>

    </Box>
    )
}

export default ShowLess