const workflowLogicWithAccount = {



    onClickMainButton: (props) => {

        props.setLocalState({ ...props.localState, SPINNER: true })
        // const callback = () => {props.setLocalState({...props.localState, SPINNER: false})}

        console.log('@PRESS BUTTON')
        console.log(props)

        const prix = props.eventData.prix
        let remise = null
        let promotionCategory = null
        let remiseConferencier = 0

        if (props.promoCode == 14) {

           // HANDLING CASE OF 15% DISCOUNT GIVEN BY 'CONFERENCIER'
          
            remiseConferencier = Math.round(0.5 * prix * 100) / 100
            promotionCategory = 'PROMO4'+props.promoId.slice(0,8)

        }
        else {
            const isValid = props.parsed?.valid

            remise = isValid && (prix => 5) ? getReduction(props.parsed, props.authState) : 0
            promotionCategory = props.parsed?.cat

        }




        let newProps

        newProps = promotionCategory ? { ...props, remise: remise, remiseConferencier: remiseConferencier, promotionCategory: promotionCategory } : { ...props, remise: remise }

       
        //TOTO PUT BACK - APPLEDEV
        initiatePaiement(newProps)

    }

}

