const workflowLogicNouveauMdp = {
    onClickMainButton: (props) => {

        console.log('@FORGOT-A')

        const { localState, setLocalState } = props
        AuthAPI({
            email: localState.EMAIL,
            callback: () => {
                console.log('@CONF-B')
                localStateAPI(props).moveTo(PAGE_CONFIRMATION, 'A')
            },
            onError: (err) => {

                let errorMessage = '-- problème de connexion --'
                console.log('@ERROR')
                console.log(err?.toString())
                const error1 = 'LimitExceededException'
                const error2 = 'Cannot reset password for the user as there is no registered/verified email or phone_number'
                const error3 = 'UserNotFoundException:'
                if (err?.toString().includes(error1))
                    errorMessage = 'Nombre de tentatives excessif. Essayez à nouveau plus tard.'
                if (err?.toString().includes(error2))
                    errorMessage = 'Impossible de réinitialiser le mot de passe (compte non confirmé). Confirmez votre compte en vous connectant.'
                if (err?.toString().includes(error3))
                    errorMessage = 'erreur: login inexistant.'
                setLocalState({ ...localState, [ERROR_MESSAGE]: errorMessage })
            }
        }).forgot()

    },

    onClickButton: (props) => {
        const { localState } = props
        localStateAPI(props).displayErrorMessage('')
        AuthAPI({
            email: localState.EMAIL,
            callback: () => {
                localStateAPI(props).displayErrorMessage('un nouveau code vous a été envoyé.')
            },
            onError: (err) => {
                console.log('@ERROR - R')
                console.log(err)
                localStateAPI(props).displayErrorMessage('problème de connexion')
            }
        }).resendSignUp()
    }
}

