const workflow = []
pages.scope.forEach(pageItem => {
    pageItem.stagesLogic.forEach(stage => workflow.push({
        page: {
            workflowLogic: pageItem.workflowLogic
        },
        workflowLogic: stage.workflowLogic,
        component: pageItem.displayComponent,
        name: pageItem.name + stage.name
    }))
})



export default workflow

